.wp-block-zolo-review {
    --zolo-item-gap: clamp(2.5rem, 8vw, 6.25rem);
    --zolo-img-size: clamp(12.5rem, 32vw, 25rem);
    --zolo-img-radius: 50%;
    --zolo-review-icon-margin: 0 0 clamp(0.625rem, 1vw, 0.938rem);
    --zolo-star-rating-item-gap: 6px;
    --zolo-star-rating-icon-size: clamp(0.875rem, 1.5vw, 1rem);
    --zolo-star-rating-icon-color: #ff9a00;
    --zolo-desc-text-color: #656565;
    --zolo-desc-margin: 0 0 clamp(1.875rem, 5vw, 3.125rem);
    --zolo-name-text-color: #19191c;
    --zolo-name-margin: 0 0 2px;
    --zolo-dasignation-text-color: #656565;
    --zolo-quote-icon-size: 24px;
    --zolo-quote-icon-color: #f6f6f6;
    --zolo-quote-icon-padding: clamp(0.625rem, 1.5vw, 1rem);
    --zolo-quote-icon-border-radius: 50%;

    box-sizing: border-box;
    // overflow: hidden;
    .zolo-item {
        display: flex;
        align-items: center;
        gap: var(--zolo-item-gap);
        text-align: center;
        flex-direction: column;
    }

    .zolo-info-wrap {
        width: 100%;
    }

    .zolo-image-quote-wrap {
        position: relative;
    }

    .zolo-image-wrap {
        position: relative;
        box-sizing: border-box;
        width: var(--zolo-img-size);
        height: var(--zolo-img-size);
        min-width: var(--zolo-img-size);
        border-radius: var(--zolo-img-radius);
        overflow: hidden;
    }

    .zolo-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // rating
    .zolo-review-icon {
        margin: var(--zolo-review-icon-margin);
    }

    .zolo-star-rating {
        display: inline-flex;
        gap: var(--zolo-star-rating-item-gap);
        svg {
            width: var(--zolo-star-rating-icon-size);
            fill: var(--zolo-star-rating-icon-color);
        }
    }

    .zolo-desc {
        font-size: clamp(1.125rem, 2.5vw, 1.625rem);
        color: var(--zolo-desc-text-color);
        font-weight: 300;
        margin: var(--zolo-desc-margin);
    }

    .zolo-name {
        font-size: clamp(1.25rem, 2vw, 1.625rem);
        color: var(--zolo-name-text-color);
        text-decoration: none;
        font-weight: 500;
        margin: var(--zolo-name-margin);
        display: block;
    }

    .zolo-designation {
        font-size: 14px;
        color: var(--zolo-dasignation-text-color);
    }

    .zolo-quote-icon {
        position: absolute;
        bottom: 0;
        background-color: var(--zoloblocks-brand-color);
        padding: var(--zolo-quote-icon-padding);
        border-radius: var(--zolo-quote-icon-border-radius);
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: content-box;
        svg {
            width: var(--zolo-quote-icon-size);
            height: var(--zolo-quote-icon-size);
            fill: var(--zolo-quote-icon-color);
            display: block;
            box-sizing: content-box;
        }
    }

    // style 1 css start here
    &.default {
        .zolo-quote-icon {
            left: 0;
            transform: translate(40%, -8%);
        }
    }
    // style 2 css start here
    &.style-1 {
        .zolo-quote-icon {
            right: 0;
            transform: translate(-40%, -8%);
        }
    }

    // style preset 2 css start here
    &.style-preset-2 {
        --zolo-quote-icon-color: rgba(221, 221, 221, 0.36);
        --zolo-quote-icon-size: 120px;
        --zolo-img-size: clamp(4rem, 8vw, 6.875rem);
        --zolo-desc-margin: 0 0 clamp(1.875rem, 5vw, 3.125rem);
        --zolo-star-rating-icon-size: 22px;
        .zolo-item {
            display: block;
            background-color: #fff;
            padding: 30px;
            border-radius: 6px;
            border: 1px solid #efefef;
            text-align: left;
            &:hover {
                .zolo-star-rating {
                    svg {
                        transform: scale(1.3);
                    }
                }
            }
        }
        .zolo-image-wrap {
            width: var(--zolo-img-size);
            height: var(--zolo-img-size);
            min-width: var(--zolo-img-size);
            border: 6px solid #fff;
            box-shadow: 0 0 10px #d2d2d4e8;
            margin-right: 0;
            margin-bottom: 20px;
        }
        .zolo-desc-quote-wrap {
            position: relative;
        }
        .zolo-quote-icon {
            display: block;
            position: absolute;
            background-color: transparent;
            padding: 0;
            svg {
                fill: var(--zolo-quote-icon-color);
                width: var(--zolo-quote-icon-size);
                height: var(--zolo-quote-icon-size);
            }
        }
        .zolo-info-wrap {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
        }
        .zolo-meta-content {
            display: inline-flex;
            align-items: center;
            flex-direction: row-reverse;
            width: 100%;
            box-sizing: border-box;
            border-top: 2px solid rgba(198, 196, 209, 0.25);
            padding-top: 20px;
        }

        .zolo-user-info-wrap {
            width: 100%;
        }
        .zolo-review-icon {
            margin: 0;
        }
        .zolo-desc {
            font-style: italic;
            margin: var(--zolo-desc-margin);
        }
        .zolo-star-rating {
            gap: 10px;
            svg {
                width: var(--zolo-star-rating-icon-size);
                transform: scale(1);
                transition: transform 0.3s ease;
            }
            svg:nth-child(2) {
                transition-delay: 0.2s;
            }
            svg:nth-child(3) {
                transition-delay: 0.3s;
            }
            svg:nth-child(4) {
                transition-delay: 0.4s;
            }
            svg:nth-child(5) {
                transition-delay: 0.5s;
            }
        }
    }

    // style preset 3 css start here

    &.style-preset-3 {
        --zolo-item-gap: 10px;
        --zolo-img-size: clamp(14.5rem, 28vw, 20rem);
        --zolo-img-radius: 20px;
        --zolo-desc-margin: 0 0 clamp(1.875rem, 4vw, 2rem);
        --zolo-desc-text-color: #efeff0;
        --zolo-name-text-color: #ffffff;
        --zolo-dasignation-text-color: #e0e0e0;
        --arrow-border-radius: 50%;
        --arrow-size: 30px;
        --arrow-h-space: 50%;
        --arrow-v-space: -20px;
        --zolo-quote-icon-size: 70px;
        --zolo-quote-icon-color: #ff9a00;
        .zolo-item {
            flex-direction: column;
            height: 100%;
            align-items: center;
        }
        .zolo-image-wrap {
            width: var(--zolo-img-size);
            height: var(--zolo-img-size);
            min-width: var(--zolo-img-size);
            border-radius: var(--zolo-img-radius);
        }
        .zolo-info-wrap {
            padding: clamp(20px, 2.5vw, 30px);
            border-radius: 20px;
            position: relative;
            background: var(--zoloblocks-brand-color);
            box-sizing: border-box;
        }
        .zolo-desc {
            font-size: 18px;
            margin: var(--zolo-desc-margin);
            color: var(--zolo-desc-text-color);
        }
        .zolo-name {
            font-size: 18px;
            color: var(--zolo-name-text-color);
        }
        .zolo-designation {
            color: var(--zolo-dasignation-text-color);
        }
        .zolo-quote-icon {
            right: 0;
            padding: 0;
            background: transparent;
            transform: rotate(-180deg);
            margin: 0 40px -28px 0;
            svg {
                width: var(--zolo-quote-icon-size);
                height: var(--zolo-quote-icon-size);
                fill: var(--zolo-quote-icon-color);
            }
        }
        .zolo-triangle {
            position: absolute;
            top: var(--arrow-v-space);
            left: var(--arrow-h-space);
            width: var(--arrow-size);
            height: var(--arrow-size);
            border-top-right-radius: var(--arrow-border-radius);
            background-color: var(--zoloblocks-brand-color);
            text-align: left;
            display: inline-block;
            transform: rotate(60deg) skewX(-30deg) scale(1, 0.866) translateX(-50%);
            transform-origin: 45% 75%;
            &::before,
            &::after {
                content: '';
                position: absolute;
                background-color: inherit;
                width: 100%;
                height: 100%;
                border-top-right-radius: inherit;
            }
            &::before {
                transform: rotate(-135deg) skewX(-45deg) scale(1.414, 0.707) translate(0, -50%);
            }
            &::after {
                transform: rotate(135deg) skewY(-45deg) scale(0.707, 1.414) translate(50%);
            }
        }
    }
}

@media (min-width: 768px) {
    .wp-block-zolo-review {
        .zolo-item {
            text-align: left;
        }
        &.default {
            .zolo-item {
                flex-direction: row-reverse;
            }
            .zolo-quote-icon {
                transform: translate(20px, -10px);
            }
        }
        &.style-1 {
            .zolo-item {
                flex-direction: row;
            }
            .zolo-quote-icon {
                transform: translate(-20px, -10px);
            }
        }
        &.style-preset-2 {
            .zolo-item {
                padding: 40px 50px 50px 50px;
            }
            .zolo-image-wrap {
                margin-right: 20px;
                margin-bottom: 0;
            }
            .zolo-info-wrap {
                flex-direction: row;
                align-items: center;
            }
        }

        &.style-preset-3 {
            --arrow-h-space: -20px;
            --arrow-v-space: 50px;
            .zolo-item {
                flex-direction: row;
                align-items: flex-end;
            }
            .zolo-triangle {
                top: unset;
                bottom: var(--arrow-v-space);
                left: var(--arrow-h-space);
                transform: rotate(90deg) skewX(-30deg) scale(1, 0.866);
            }
        }
    }
}

@media (min-width: 1025px) {
    .wp-block-zolo-review {
        &.default {
            .zolo-quote-icon {
                transform: translate(30px, -30px);
            }
        }
        &.style-1 {
            .zolo-quote-icon {
                transform: translate(-30px, -30px);
            }
        }
    }
}
